*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    font-size: 10px;
}
.body{
   
    max-width: 1300px;
    margin: auto;
    background-image: linear-gradient(white,rgb(240, 237, 102));
}
.rewardout{
    position: relative;
    overflow: hidden;
}
.reward{
   padding: auto;
}
@media (max-width:400px) {
    
.reward{
    max-height: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
 }
 
}

img{
    object-fit: cover;
}

.img3{
    max-width: 75%;
    margin: auto;
}
.hr {
    color: red;
    border-top:2px solid red;
    opacity: 1.2;
}

.services{
    display: flex; 
      /* using css flex  */
    flex-direction: column;
}
footer > svg,p{
    padding: 5px;
    display: inline;
}